import React from "react";
import {
    Box,
    Card,
    CardContent,
    Checkbox,
    Grid,
    Typography,
} from "@mui/material";

import moment from "moment";

const sx = {
    root: {
        flex: 1,
        alignItems: "center",
        height: "100%",
        boxSizing: "border-box",
        overflow: "hidden",
        display: "flex",
        flexDirection: "column",
    },
    header: {
        marginBottom: 2,
        flexShrink: 0,
    },
    grid: {
        flexDirection: "row",
        justifyContent: "center",
        alignItems: "center",
        maxHeight: "100vh",
        boxSizing: "border-box",
        overflow: "auto",
        paddingBottom: 2,
        paddingLeft: 1,
    },
};

const EntityGroup = ({ data, schema }) => {
    const { items } = schema;

    return (
        <Box sx={sx.root}>
            <Typography
                sx={sx.header}
                gutterBottom
                color="textPrimary"
                variant="h6"
            >
                {schema.label}
            </Typography>
            <Grid sx={sx.grid} container spacing={3}>
                {items.map((row, index) => {
                    const { type, label, field, xs, sm, md, lg, xl } = row;
                    if (row.type === "hidden") return null;
                    return (
                        <Grid
                            key={index}
                            item
                            xs={xs || 12}
                            sm={sm}
                            md={md}
                            lg={lg}
                            xl={xl}
                        >
                            <Card key={index}>
                                <CardContent>
                                    {type === "number" && (
                                        <Typography
                                            variant="body2"
                                            component="p"
                                        >
                                            <b>{label}:</b> {data[field]}
                                        </Typography>
                                    )}

                                    {type === "string" && (
                                        <Typography
                                            variant="body2"
                                            component="p"
                                        >
                                            <b>{label}:</b> {data[field]}
                                        </Typography>
                                    )}

                                    {type === "date" && (
                                        <Typography
                                            variant="body2"
                                            component="p"
                                        >
                                            <b>{label}:</b> {data[field]}
                                        </Typography>
                                    )}

                                    {type === "datetime" && (
                                        <Typography
                                            variant="body2"
                                            component="p"
                                        >
                                            <b>{label}:</b>{" "}
                                            {moment(data[field]).format(
                                                "YYYY-MM-DD h:mm a"
                                            )}
                                        </Typography>
                                    )}

                                    {type === "text" && (
                                        <Typography
                                            variant="body2"
                                            component="p"
                                        >
                                            <b>{label}:</b> {data[field]}
                                        </Typography>
                                    )}

                                    {type === "boolean" && (
                                        <>
                                            <Checkbox
                                                checked={data[field]}
                                                readOnly
                                                color="primary"
                                                inputProps={{
                                                    "aria-label":
                                                        "primary checkbox",
                                                }}
                                            />
                                            <label>{label}</label>
                                        </>
                                    )}
                                </CardContent>
                            </Card>
                        </Grid>
                    );
                })}
            </Grid>
        </Box>
    );
};

export default EntityGroup;
