import React, { useState } from "react";
import {
    Box,
    Divider,
    Drawer,
    List,
    ListItem,
    ListItemIcon,
    ListItemText,
} from "@mui/material";

import LogoutIcon from "@mui/icons-material/MeetingRoom";
import AccountCircle from "@mui/icons-material/AccountCircle";
import Assignment from "@mui/icons-material/Assignment";
import AssignmentInd from "@mui/icons-material/AssignmentInd";
import ContactMail from "@mui/icons-material/ContactMail";
import PersonAdd from "@mui/icons-material/PersonAdd";
import VpnKey from "@mui/icons-material/VpnKey";

import { AuthAPI } from "../API";

import PatientModal from "./PatientModal";
import ConfirmModal from "./ConfirmModal";
import HCModal from "./HCModal";
import RegisterModal from "./RegisterModal";
import VerifyModal from "./VerifyModal";
import ChangePasswordModal from "./ChangePasswordModal";
import DataModal from "./DataModal";
import Main from "../views/Main";

const sx = {
    list: {
        minWidth: 250,
        padding: 2,
        width: "auto",
    },
};

const SideMenu = ({ onAuth }) => {
    const [open, setOpen] = useState(false);

    const toggleDrawer = (open) => (event) => {
        if (
            event.type === "keydown" &&
            (event.key === "Tab" || event.key === "Shift")
        ) {
            return;
        }
        setOpen(open);
    };

    const logout = () => {
        AuthAPI.logout(() => {
            Main.edit(false);
            onAuth(false);
        });
    };

    SideMenu.show = () => {
        setOpen(true);
    };
    SideMenu.hide = () => {
        setOpen(false);
    };

    return (
        <Drawer
            disablePortal
            disableEnforceFocus
            disableAutoFocus
            anchor="right"
            open={open}
            transitionDuration={360}
            onClose={toggleDrawer(false)}
        >
            <Box
                sx={sx.list}
                role="presentation"
                onClick={toggleDrawer(false)}
                onKeyDown={toggleDrawer(false)}
            >
                <List>
                    <ListItem button onClick={PatientModal.show}>
                        <ListItemIcon>
                            <AssignmentInd />
                        </ListItemIcon>
                        <ListItemText primary="Paciente" />
                    </ListItem>
                    <ListItem button onClick={() => HCModal.show()}>
                        <ListItemIcon>
                            <Assignment />
                        </ListItemIcon>
                        <ListItemText primary="Historia" />
                    </ListItem>
                </List>
                <Divider />
                <List>
                    <ListItem button onClick={RegisterModal.show}>
                        <ListItemIcon>
                            <PersonAdd />
                        </ListItemIcon>
                        <ListItemText primary="Registrar usuario" />
                    </ListItem>
                    <ListItem button onClick={VerifyModal.show}>
                        <ListItemIcon>
                            <ContactMail />
                        </ListItemIcon>
                        <ListItemText primary="Verificar usuario" />
                    </ListItem>
                </List>
                <Divider />
                <List>
                    <ListItem button onClick={DataModal.show}>
                        <ListItemIcon>
                            <AccountCircle />
                        </ListItemIcon>
                        <ListItemText primary="Cambio de datos" />
                    </ListItem>
                    <ListItem button onClick={ChangePasswordModal.show}>
                        <ListItemIcon>
                            <VpnKey />
                        </ListItemIcon>
                        <ListItemText primary="Cambio de clave" />
                    </ListItem>
                </List>
                <Divider />
                <List>
                    <ListItem
                        button
                        onClick={() =>
                            ConfirmModal.show("Cerrar sesin", logout)
                        }
                    >
                        <ListItemIcon>
                            <LogoutIcon />
                        </ListItemIcon>
                        <ListItemText primary="Salir" />
                    </ListItem>
                </List>
            </Box>
        </Drawer>
    );
};

SideMenu.show = () => {};
SideMenu.hide = () => {};

export default SideMenu;
