import Fetch from "./Fetch.js";

const ClinicalHistoryAPI = {
    get: (data, callback = () => {}, error = () => {}) => {
        Fetch.get(
            "/api/historiaclinica/",
            data,
            (res) => {
                callback(res);
            },
            error
        );
    },
    filterList: (idPatient, page = 1, callback = () => {}, error = () => {}) => {
        Fetch.get(
            "/api/historiaclinicafilterlist/?paciente=" + idPatient + "&page=" + page,
            (res) => {
                callback(res);
            },
            error
        );
    },
    user: (callback = () => {}, error = () => {}) => {
        Fetch.get(
            "/rest-auth/user/",
            (res) => {
                callback(res);
            },
            error
        );
    },
    registration: (data, callback = () => {}, error = () => {}) => {
        Fetch.post(
            "/rest-auth/registration/",
            data,
            (res) => {
                callback(res);
            },
            error
        );
    },
    verify: (key, callback = () => {}, error = () => {}) => {
        Fetch.post(
            "/rest-auth/registration/verify-email/",
            {key: key},
            (res) => {
                callback(res);
            },
            error
        );
    },
    password: (data, callback = () => {}, error = () => {}) => {
        Fetch.put(
            "/user/password",
            data,
            (res) => {
                callback(res);
            },
            error
        );
    },
    // list: (callback = () => {}, error = () => {}) => {
    //     Fetch.get(
    //         Data.baseUrl + "/api/user/list",
    //         (res) => {
    //             Data.users = res;
    //             callback(res);
    //         },
    //         error,
    //     );
    // },
    // post: (data, callback = () => {}, error = () => {}) => {
    //     Fetch.post(
    //         Data.baseUrl + "/api/user",
    //         data,
    //         (res) => {
    //             Data.users.push(res);
    //             callback(res);
    //         },
    //         error,
    //     );
    // },
};

export default ClinicalHistoryAPI;
