import React, { useState } from "react";
import { Box, Button, Modal } from "@mui/material";

const sx = {
    modal: {
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
    },
    paper: {
        textAlign: "center",
        backgroundColor: "#fff",
        boxShadow: 5,
        padding: 3,
        zIndex: 1000,
    },
    content: {
        marginTop: 4,
        minWidth: 400,
        width: "100%",
        display: "flex",
        justifyContent: "space-around",
    },
};

let cb = () => {};

const ConfirmModal = () => {
    const [open, setOpen] = useState(false);
    const [title, setTitle] = useState("");

    const handleClose = () => {
        setOpen(false);
    };
    ConfirmModal.show = (title = "", callback = () => {}) => {
        setTitle(title);
        cb = callback;
        setOpen(true);
    };
    ConfirmModal.hide = () => {
        setOpen(false);
    };

    if (!open) return null;
    return (
        <Modal
            open={open}
            onClose={handleClose}
            sx={sx.modal}
            aria-labelledby="simple-modal-title"
            aria-describedby="simple-modal-description"
        >
            <Box sx={sx.paper}>
                <h2 id="server-modal-title">{title}</h2>
                <Box sx={sx.content}>
                    <Button
                        variant="contained"
                        color="primary"
                        onClick={() => {
                            cb();
                            handleClose();
                        }}
                    >
                        Aceptar
                    </Button>
                    <Button variant="contained" onClick={handleClose}>
                        Cancelar
                    </Button>
                </Box>
            </Box>
        </Modal>
    );
};

ConfirmModal.show = () => {};
ConfirmModal.hide = () => {};

export default ConfirmModal;
