import React, { useState } from "react";
import { Box, Backdrop, Button, Fade, Modal, TextField } from "@mui/material";
import { AuthAPI } from "../API";

const sx = {
    modal: {
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
    },
    paper: {
        backgroundColor: "#fff",
        boxShadow: 5,
        padding: 3,
        zIndex: 1000,
    },
    content: {
        marginTop: 2,
        minWidth: 400,
        width: "100%",
        display: "flex",
        justifyContent: "center",
    },
    textField: {
        marginTop: 2,
    },
    btn: {
        margin: 1,
    },
};

const VerifyModal = () => {
    const [open, setOpen] = useState(false);
    const [values, setValues] = useState({
        key: "",
    });

    const handleChange = (prop) => (event) => {
        setValues({ ...values, [prop]: event.target.value });
    };

    const handleClose = () => {
        setOpen(false);
    };
    VerifyModal.show = () => {
        setOpen(true);
        setValues({
            key: "",
        });
    };
    VerifyModal.hide = () => {
        setOpen(false);
    };

    const handleVerify = () => {
        AuthAPI.verifyEmail(values, (res) => {
            console.log(res);
        });
    };

    return (
        <Modal
            disablePortal
            disableEnforceFocus
            disableAutoFocus
            open={open}
            onClose={handleClose}
            sx={sx.modal}
            closeAfterTransition
            BackdropComponent={Backdrop}
            BackdropProps={{
                timeout: 500,
            }}
            aria-labelledby="register-modal-title"
        >
            <Fade in={open}>
                <Box sx={sx.paper}>
                    <h2 id="register-modal-title">Verificar usuario</h2>

                    <TextField
                        fullWidth
                        variant="standard"
                        id="user"
                        label="Cdigo"
                        sx={sx.textField}
                        value={values.key}
                        onChange={handleChange("key")}
                    />
                    <Box sx={sx.content}>
                        <Button
                            variant="contained"
                            color="primary"
                            sx={sx.btn}
                            onClick={() => {
                                handleVerify();
                            }}
                        >
                            Aceptar
                        </Button>
                        <Button
                            variant="contained"
                            sx={sx.btn}
                            onClick={handleClose}
                        >
                            Cancelar
                        </Button>
                    </Box>
                </Box>
            </Fade>
        </Modal>
    );
};

VerifyModal.show = () => {};
VerifyModal.hide = () => {};

export default VerifyModal;
