import React, { useState } from "react";
import {
    Backdrop,
    Box,
    Button,
    ClickAwayListener,
    Fade,
    Grid,
    Grow,
    IconButton,
    MenuItem,
    MenuList,
    Modal,
    Paper,
    Popper,
    TextField,
    Typography,
} from "@mui/material";
import {
    DataGrid,
    GridToolbarColumnsButton,
    GridToolbarContainer,
    GridToolbarDensitySelector,
    GridToolbarExport,
} from "@mui/x-data-grid";


import Add from "@mui/icons-material/Add";
import Apps from "@mui/icons-material/Apps";
import Delete from "@mui/icons-material/Delete";
import Edit from "@mui/icons-material/Edit";

import { HCAPI } from "../API";
import ConfirmModal from "./ConfirmModal";
import Notifications from "./Notifications";
import { LoadingOverlay, NoRowsOverlay } from "./CustomDataGrid";
import Data from "../globals/Data";
import Entity from "../Entity";
import Main from "../views/Main";
import PatientModal from "./PatientModal";
import EntityContent from "./EntityContent";

const sx = {
    modal: {
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
    },
    paper: {
        backgroundColor: "#fff",
        boxShadow: 5,
        padding: 3,
        zIndex: 1000,
    },
    content: {
        height: 400,
        minWidth: 350,
        width: "80vw",
        overflow: "auto",
    },
    textField: {
        marginTop: 2,
    },
    btnBox: {
        marginTop: 2,
    },
    btn: {
        margin: "0 10px",
    },
    relatedContainer: {
        height: "100%",
        width: "100%",
        display: "flex",
        flexDirection: "column",
    },
};

const deleteRow = (params) => {
    ConfirmModal.show("Eliminar historia", () => {
        const { id } = params;
        HCAPI.delete(id, () => {
            Data.hc = Data.hc.filter((e) => e.id !== id);
            HCModal.update();
        });
    });
};

const deleteRelatedRow = (params) => {
    const { label } = params.row;
    ConfirmModal.show("Eliminar " + label, () => {
        const { api, data } = params.row;
        const { id } = data;
        api.delete(id, () => {
            Data.hcRelated = Data.hcRelated.filter((e) => e.id !== id);
            HCModal.update();
        });
    });
};

const columns = [
    { field: "id", headerName: "ID", width: 90 },
    {
        field: "modelo",
        headerName: "Modelo",
        description: "Modelo",
        width: 110,
        editable: true,
    },
    {
        field: "ministerio",
        headerName: "Ministerio",
        description: "Ministerio",
        width: 130,
    },
    {
        field: "titulo",
        headerName: "Ttulo",
        description: "Ttulo",
        width: 150,
    },
    {
        field: "motivo",
        headerName: "Motivo",
        description: "Motivo",
        width: 150,
    },
    {
        field: "historia_enfermedad_actual",
        headerName: "Historia Enfermedad Actual",
        description: "Historia Enfermedad Actual",
        width: 150,
    },
    {
        field: "operations",
        headerName: "Operaciones",
        description: "Botones de accin sobre el paciente.",
        sortable: false,
        filterable: false,
        width: 160,
        renderCell: (params) => (
            <Box>
                <IconButton
                    color="primary"
                    onClick={() => HCModal.form(params)}
                >
                    <Edit />
                </IconButton>

                <IconButton color="primary">
                    <Apps />
                </IconButton>

                <IconButton color="secondary" onClick={() => deleteRow(params)}>
                    <Delete />
                </IconButton>
            </Box>
        ),
    },
];

const columnsRelated = [
    { field: "id", headerName: "ID", width: 90 },
    {
        field: "label",
        headerName: "Ttulo",
        description: "Ttulo",
        width: 240,
        editable: true,
    },
    {
        field: "operations",
        headerName: "Operaciones",
        description: "Botones de accin sobre el paciente.",
        sortable: false,
        filterable: false,
        width: 140,
        renderCell: (params) => (
            <Box>
                <IconButton
                    color="primary"
                    onClick={() => {
                        PatientModal.hide();
                        HCModal.hide();
                        Main.edit(params.row);
                    }}
                >
                    <Edit />
                </IconButton>

                <IconButton
                    color="secondary"
                    onClick={() => deleteRelatedRow(params)}
                >
                    <Delete />
                </IconButton>
            </Box>
        ),
    },
];

const HCModal = () => {
    const update = useState(null)[1];
    const [open, setOpen] = useState(false);
    const [loading, setLoading] = useState(true);
    const [values, setValues] = useState(false);
    const [paciente, setPaciente] = useState(false);

    const handleClose = () => {
        setOpen(false);
    };
    HCModal.update = () => update(new Date());
    HCModal.show = (param = false) => {
        setValues(false);
        Data.hcRelated = [];
        if (paciente !== param) {
            Data.hc = [];
        }
        setLoading(true);
        setPaciente(param);
        if (param)
            HCAPI.filterList(
                param.id,
                undefined,
                (data) => {
                    Data.hc = data.results;
                    setLoading(false);
                },
                (err) => {
                    console.log("ERROR");
                    console.log(err);
                }
            );
        else
            HCAPI.get(
                (data) => {
                    if (data.detail) {
                        console.log("ERROR");
                        console.log(data.detail);
                        return;
                    }
                    Data.hc = data;
                    setLoading(false);
                },
                (err) => {
                    console.log("ERROR");
                    console.log(err);
                }
            );
        setOpen(true);
    };
    HCModal.hide = () => {
        setOpen(false);
    };
    HCModal.form = (params) => {
        if (params) {
            setValues(params.row);
        } else {
            setValues({
                id: "",
                modelo: "",
                ministerio: "",
                titulo: "",
                motivo: "",
                historia_enfermedad_actual: "",
                paciente: paciente ? paciente.id : "",
            });
        }
    };

    return (
        <Modal
            disablePortal
            disableEnforceFocus
            disableAutoFocus
            aria-labelledby="transition-modal-title"
            sx={sx.modal}
            open={open}
            onClose={handleClose}
            closeAfterTransition
            BackdropComponent={Backdrop}
            BackdropProps={{
                timeout: 500,
            }}
        >
            <Fade in={open}>
                <Box sx={sx.paper}>
                    <h2 id="transition-modal-title">
                        Historia Clnica
                        {paciente &&
                            ` - ${paciente.row.nombre} ${paciente.row.primer_apellido} ${paciente.row.segundo_apellido}`}
                    </h2>
                    <Box sx={sx.content}>
                        {values && (
                            <HCForm
                                values={values}
                                setValues={setValues}
                            />
                        )}
                        <ValueGetterGrid loading={loading} showAdd={paciente} />
                    </Box>
                </Box>
            </Fade>
        </Modal>
    );
};

HCModal.update = () => {};
HCModal.show = () => {};
HCModal.hide = () => {};
HCModal.form = () => {};

export default HCModal;

const HCForm = ({ values, setValues }) => {
    const {
        id,
        modelo,
        ministerio,
        titulo,
        motivo,
        historia_enfermedad_actual,
        paciente,
    } = values;

    const accept = () => {
        if (
            [
                modelo.trim(),
                ministerio.trim(),
                titulo.trim(),
                motivo.trim(),
                historia_enfermedad_actual.trim(),
            ].includes("")
        )
            return Notifications.error("No puede haber campos vacos");
        const fun =
            id === ""
                ? HCAPI.post
                : (data, cb, e) => HCAPI.put(id, data, cb, e);
        ConfirmModal.show("Guardar cambios", () =>
            fun(
                {
                    modelo,
                    ministerio,
                    titulo,
                    motivo,
                    historia_enfermedad_actual,
                    paciente,
                },
                (res) => {
                    if (id === "") Data.hc = [...Data.hc, res];
                    else Data.hc = Data.hc.map((e) => (e.id === id ? res : e));
                    setValues(false);
                    HCModal.update();
                }
            )
        );
    };

    const handleChange = (prop) => (event) => {
        const { value } = event.target;
        setValues({ ...values, [prop]: value });
    };

    return (
        <Grid container className="hide-next">
            <Grid item xs={12}>
                <label>
                    {id === ""
                        ? "Agregar historia clnica"
                        : "Editar historia clnica"}
                </label>
            </Grid>
            <Grid container item xs={12} spacing={2}>
                <Grid item xs={12} sm={6}>
                    <TextField
                        fullWidth
                        variant="standard"
                        label="Modelo"
                        inputProps={{ maxLength: 200 }}
                        sx={sx.textField}
                        value={modelo}
                        onChange={handleChange("modelo")}
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        fullWidth
                        variant="standard"
                        label="Ministerio"
                        inputProps={{ maxLength: 200 }}
                        sx={sx.textField}
                        value={ministerio}
                        onChange={handleChange("ministerio")}
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        fullWidth
                        variant="standard"
                        label="Titulo"
                        inputProps={{ maxLength: 200 }}
                        sx={sx.textField}
                        value={titulo}
                        onChange={handleChange("titulo")}
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        fullWidth
                        variant="standard"
                        label="Historia enfermedad actual"
                        inputProps={{ maxLength: 3 }}
                        sx={sx.textField}
                        value={historia_enfermedad_actual}
                        onChange={handleChange("historia_enfermedad_actual")}
                    />
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        fullWidth
                        variant="standard"
                        label="Motivo"
                        inputProps={{ maxLength: 600 }}
                        multiline
                        sx={sx.textField}
                        value={motivo}
                        onChange={handleChange("motivo")}
                    />
                </Grid>
            </Grid>
            <Grid
                container
                justifyContent="center"
                spacing={2}
                item
                xs={12}
                sx={sx.btnBox}
            >
                <Button
                    variant="contained"
                    color="primary"
                    sx={sx.btn}
                    onClick={accept}
                >
                    Aceptar
                </Button>
                <Button
                    variant="contained"
                    sx={sx.btn}
                    onClick={() => setValues(false)}
                >
                    Cancelar
                </Button>
            </Grid>
        </Grid>
    );
};

const CustomToolbar = (showAdd) => () => {
    return (
        <GridToolbarContainer>
            {showAdd && (
                <Button
                    variant="contained"
                    color="primary"
                    size="small"
                    startIcon={<Add />}
                    onClick={() => HCModal.form()}
                >
                    Agregar
                </Button>
            )}
            <GridToolbarColumnsButton />
            <GridToolbarDensitySelector />
            <GridToolbarExport />
        </GridToolbarContainer>
    );
};

const mergeSchema = (schema) => {
    let arr = {};
    schema.forEach((row) => {
        row.items.forEach((row) => {
            if (row.type === "boolean") arr[row.field] = false;
            else if (row.type === "number") arr[row.field] = 0;
            else arr[row.field] = "";
        });
    });
    return arr;
};

const CustomToolbarRel = (selected, open, setOpen, anchorRef) => () => {
    const handleToggle = () => {
        setOpen((prevOpen) => !prevOpen);
    };

    const handleClose = (event) => {
        if (anchorRef.current && anchorRef.current.contains(event.target)) {
            return;
        }

        setOpen(false);
    };

    function handleListKeyDown(event) {
        if (event.key === "Tab") {
            event.preventDefault();
            setOpen(false);
        }
    }

    return (
        <GridToolbarContainer>
            <GridToolbarContainer
                style={{
                    width: "100%",
                    justifyContent: "space-between",
                }}
            >
                <Typography noWrap align="center" variant="h6">
                    {selected.id} - {selected.row.titulo}
                </Typography>
                <Button
                    ref={anchorRef}
                    aria-controls={open ? "menu-list-grow" : undefined}
                    aria-haspopup="true"
                    variant="contained"
                    color="primary"
                    size="small"
                    onClick={handleToggle}
                >
                    Agregar documento
                </Button>
                <Popper
                    open={open}
                    anchorEl={anchorRef.current}
                    role={undefined}
                    transition
                    disablePortal
                    style={{
                        zIndex: 10,
                    }}
                >
                    {({ TransitionProps, placement }) => (
                        <Grow
                            {...TransitionProps}
                            style={{
                                transformOrigin:
                                    placement === "bottom"
                                        ? "center top"
                                        : "center bottom",
                            }}
                        >
                            <Paper>
                                <ClickAwayListener onClickAway={handleClose}>
                                    <MenuList
                                        autoFocusItem={open}
                                        id="menu-list-grow"
                                        onKeyDown={handleListKeyDown}
                                    >
                                        {Entity.map((row) => (
                                            <MenuItem
                                                key={row.label}
                                                onClick={() => {
                                                    PatientModal.hide();
                                                    HCModal.hide();
                                                    EntityContent.clearError();
                                                    Main.edit({
                                                        ...row,
                                                        data: {
                                                            ...mergeSchema(
                                                                row.schema
                                                            ),
                                                            id: undefined,
                                                            historia:
                                                                selected.row.id,
                                                        },
                                                    });
                                                }}
                                            >
                                                {row.label}
                                            </MenuItem>
                                        ))}
                                    </MenuList>
                                </ClickAwayListener>
                            </Paper>
                        </Grow>
                    )}
                </Popper>
            </GridToolbarContainer>
        </GridToolbarContainer>
    );
};

function ValueGetterGrid({ loading, showAdd }) {
    const [selected, setSelected] = useState(false);
    const [open, setOpen] = React.useState(false);
    const anchorRef = React.useRef(null);
    const update = useState(null)[1];

    const handleSelect = (row) => {
        setSelected(row);
        Data.hcRelated = [];
        const draw = Data.relatedDraw + 1;
        Data.relatedDraw++;
        if ((row.id !== null, row.id !== "")) {
            Entity.forEach((entity) => {
                if (draw < Data.relatedDraw) return;
                entity.api.filterList(row.id, undefined, (res) => {
                    if (draw < Data.relatedDraw) return;
                    if (res.count === 0) return;
                    res.results.forEach((element) => {
                        Data.hcRelated = [
                            ...Data.hcRelated,
                            {
                                id: Data.hcRelated.length,
                                ...entity,
                                data: element,
                            },
                        ];
                    });
                    update(new Date());
                });
            });
        }
    };

    return (
        <Grid
            container
            className="hide-next"
            alignItems={"stretch"}
            style={{ height: "100%", width: "100%" }}
        >
            <Grid item xs={12} sm={6}>
                <DataGrid
                    rows={Data.hc}
                    columns={columns}
                    loading={loading}
                    density="compact"
                    onRowClick={handleSelect}
                    components={{
                        LoadingOverlay: LoadingOverlay,
                        NoRowsOverlay: NoRowsOverlay,
                        Toolbar: CustomToolbar(showAdd),
                    }}
                />
            </Grid>
            <Grid item xs={12} sm={6}>
                <Grid
                    container
                    className="hide-next"
                    alignItems="center"
                    justifyContent="center"
                    style={{ height: "100%", width: "100%" }}
                >
                    {!selected && (
                        <Typography noWrap color="textSecondary" variant="h5">
                            Seleccione una historia
                        </Typography>
                    )}
                    {selected && (
                        <Box sx={sx.relatedContainer}>
                            <DataGrid
                                rows={Data.hcRelated}
                                columns={columnsRelated}
                                loading={loading}
                                density="compact"
                                components={{
                                    LoadingOverlay: LoadingOverlay,
                                    NoRowsOverlay: NoRowsOverlay,
                                    Toolbar: CustomToolbarRel(
                                        selected,
                                        open,
                                        setOpen,
                                        anchorRef
                                    ),
                                }}
                            />
                        </Box>
                    )}
                </Grid>
            </Grid>
        </Grid>
    );
}
