import React, { useState } from "react";
import {
    Autocomplete,
    CircularProgress,
    Divider,
    Grid,
    Hidden,
    IconButton,
    InputBase,
    Paper,
    Typography,
} from "@mui/material";

import HelpIcon from "@mui/icons-material/Help";
import SearchIcon from "@mui/icons-material/Search";
import RestoreIcon from "@mui/icons-material/SettingsBackupRestore";

import { pacienteAPI } from "../API";

const sx = {
    root: {
        padding: "2px 4px",
        display: "flex",
        alignItems: "center",
        marginBottom: 1,
    },
    group: {
        justifyContent: "center",
    },
    input: {
        marginLeft: 1,
        flex: 1,
    },
    reset: {
        paddingLeft: "0 !important",
    },
    iconButton: {
        padding: 1,
    },
    patientBox: {
        marginBottom: "20px",
    },
    divider: {
        height: 28,
        margin: 1,
        marginTop: 3,
    },
    list: {
        width: "100%",
        backgroundColor: "#fff",
    },
    formControl: {
        margin: 1,
        minWidth: 260,
    },
    patient: {
        padding: 1,
        marginLeft: 1,
    },
};

const getOptionSelected = (option, value) => {
    return option.id === value.id;
};

let autocompleteList = [];

const SearchBox = ({ patient, onPatientChange, onClear }) => {
    const update = useState(null)[1];
    const [open, setOpen] = useState(false);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(false);
    const [value, setValue] = useState("");

    const filter = (dni) => {
        setError(false);
        setLoading(true);
        pacienteAPI.filterList(
            dni,
            undefined,
            (res) => {
                autocompleteList = [];
                res.results.forEach((row) => {
                    if (!autocompleteList.find((e) => e.id === row.id))
                        autocompleteList.push(row);
                });
                autocompleteList.sort((a, b) => a.dni.localeCompare(b.dni));
                setError(false);
                update(new Date());
                setLoading(false);
            },
            (res) => {
                setError(true);
            }
        );
    };

    const onChange = (e) => {
        filter(e.target.value);
        setValue(e.target.value);
    };

    const onSearch = () => {
        filter(value);
        setOpen(true);
    };

    const handleOpen = (e) => {
        if (e.type !== "mousedown") setOpen(true);
    };
    const handleClose = (e) => {
        if (e.type !== "mousedown") setOpen(false);
    };

    return (
        <Paper component="form" sx={sx.root}>
            <Grid
                container
                item
                xs={12}
                sm={12}
                md
                alignItems="center"
                sx={sx.group}
                spacing={2}
            >
                <Grid
                    container
                    item
                    xs={12}
                    sm={12}
                    md
                    alignItems="center"
                    sx={sx.group}
                >
                    <IconButton sx={sx.iconButton} aria-label="help">
                        <HelpIcon />
                    </IconButton>
                    <Autocomplete
                        options={autocompleteList}
                        getOptionSelected={getOptionSelected}
                        getOptionLabel={(option) => option.dni}
                        style={{ flex: 1, minWidth: 200 }}
                        open={open}
                        onOpen={handleOpen}
                        onClose={handleClose}
                        onChange={onPatientChange}
                        clearOnEscape
                        autoComplete
                        autoHighlight
                        loading={loading}
                        loadingText={error ? "Error" : "Cargando"}
                        renderInput={(params) => (
                            <div ref={params.InputProps.ref}>
                                <InputBase
                                    sx={sx.input}
                                    placeholder="Seleccione carn de paciente"
                                    onKeyUp={onChange}
                                    onChange={onChange}
                                    disabled={false}
                                    fullWidth={true}
                                    type="text"
                                    {...params.inputProps}
                                    onKeyDown={(e) =>
                                        e.key === "Enter" && e.preventDefault()
                                    }
                                    endAdornment={
                                        <React.Fragment>
                                            {loading ? (
                                                <CircularProgress
                                                    color="inherit"
                                                    size={20}
                                                />
                                            ) : (
                                                params.InputProps.endAdornment
                                            )}
                                        </React.Fragment>
                                    }
                                />
                            </div>
                        )}
                    />
                    <IconButton
                        sx={sx.iconButton}
                        aria-label="search"
                        onClick={onSearch}
                    >
                        <SearchIcon />
                    </IconButton>
                </Grid>

                <Grid
                    container
                    item
                    xs
                    sm
                    md
                    alignItems="center"
                    wrap="nowrap"
                    spacing={2}
                >
                    <Hidden smDown>
                        <Divider sx={sx.divider} orientation="vertical" />
                    </Hidden>
                    <Grid item sx={sx.reset}>
                        <IconButton
                            color="primary"
                            sx={sx.iconButton}
                            aria-label="directions"
                            onClick={onClear}
                        >
                            <RestoreIcon />
                        </IconButton>
                    </Grid>
                    <Divider sx={sx.divider} orientation="vertical" />
                    <Grid item xs zeroMinWidth>
                        <Grid item xs zeroMinWidth>
                            <Typography
                                noWrap
                                color="textSecondary"
                                sx={sx.patient}
                            >
                                {patient
                                    ? `${patient.nombre} ${patient.primer_apellido} ${patient.segundo_apellido}`
                                    : "[Seleccione un paciente]"}
                            </Typography>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </Paper>
    );
};

export default SearchBox;
