import React, { useState } from "react";
import { Backdrop, Box, Button, Fade, Modal, TextField } from "@mui/material";
import { AuthAPI } from "../API";
import Data from "../globals/Data";
import Notifications from "./Notifications";

const sx = {
    modal: {
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
    },
    paper: {
        backgroundColor: "#fff",
        boxShadow: 5,
        padding: 3,
        zIndex: 1000,
    },
    content: {
        marginTop: 2,
        minWidth: 400,
        width: "100%",
        display: "flex",
        justifyContent: "center",
    },
    textField: {
        marginTop: 2,
    },
    btn: {
        margin: 1,
    },
};

const ChangePasswordModal = () => {
    const [open, setOpen] = useState(false);
    const [values, setValues] = useState({
        new_password1: "",
        new_password2: "",
    });

    const handleChange = (prop) => (event) => {
        setValues({ ...values, [prop]: event.target.value });
    };

    const handleClose = () => {
        setOpen(false);
    };
    ChangePasswordModal.show = () => {
        setOpen(true);
        setValues({
            new_password1: "",
            new_password2: "",
        });
    };
    ChangePasswordModal.hide = () => {
        setOpen(false);
    };

    const handleChangePassword = () => {
        AuthAPI.passwordChange(values, (res) => {
            let tk = atob(Data.token);
            tk = tk.split(":");
            tk[1] = values.new_password1;
            Data.token = btoa(tk.join(":"));
            Data.save();
            Notifications.success("Contrasea actualizada");
            setOpen(false);
        });
    };

    return (
        <Modal
            disablePortal
            disableEnforceFocus
            disableAutoFocus
            aria-labelledby="transition-modal-title"
            sx={sx.modal}
            open={open}
            onClose={handleClose}
            closeAfterTransition
            BackdropComponent={Backdrop}
            BackdropProps={{
                timeout: 500,
            }}
        >
            <Fade in={open}>
                <Box sx={sx.paper}>
                    <h2 id="register-modal-title">Cambio de contrasea</h2>

                    <TextField
                        fullWidth
                        variant="standard"
                        id="user"
                        label="Contrasea"
                        sx={sx.textField}
                        value={values.new_password1}
                        type="password"
                        onChange={handleChange("new_password1")}
                    />
                    <TextField
                        fullWidth
                        variant="standard"
                        id="user"
                        label="Verificar contrasea"
                        sx={sx.textField}
                        value={values.new_password2}
                        type="password"
                        onChange={handleChange("new_password2")}
                    />
                    <Box sx={sx.content}>
                        <Button
                            variant="contained"
                            color="primary"
                            sx={sx.btn}
                            onClick={() => {
                                handleChangePassword();
                            }}
                        >
                            Aceptar
                        </Button>
                        <Button
                            variant="contained"
                            sx={sx.btn}
                            onClick={handleClose}
                        >
                            Cancelar
                        </Button>
                    </Box>
                </Box>
            </Fade>
        </Modal>
    );
};

ChangePasswordModal.show = () => {};
ChangePasswordModal.hide = () => {};

export default ChangePasswordModal;
