import React, { useState } from "react";
import {
    Box,
    Card,
    CardContent,
    Checkbox,
    FormControlLabel,
    Grid,
    TextField,
    Typography,
} from "@mui/material";

import AdapterMoment from "@mui/lab/AdapterMoment";
import LocalizationProvider from "@mui/lab/LocalizationProvider";
import DatePicker from "@mui/lab/DatePicker";
import DateTimePicker from "@mui/lab/DateTimePicker";

const sx = {
    root: {
        flex: 1,
        alignItems: "center",
        height: "100%",
        boxSizing: "border-box",
        overflow: "hidden",
        display: "flex",
        flexDirection: "column",
    },
    header: {
        marginBottom: 2,
        flexShrink: 0,
    },
    grid: {
        flexDirection: "row",
        justifyContent: "center",
        alignItems: "center",
        maxHeight: "100vh",
        boxSizing: "border-box",
        overflow: "auto",
        paddingBottom: 2,
        paddingLeft: 1,
    },
};

const EntityEditGroup = ({ data, schema, error }) => {
    const update = useState(null)[1];
    const { items } = schema;

    const handleChange = (prop) => (event) => {
        data[prop] = event.target.value;
        update(new Date());
    };

    const handleChangeDateTime = (prop) => (value) => {
        data[prop] = value;
        update(new Date());
    };

    const handleChangeCheck = (prop) => (event) => {
        data[prop] = event.target.checked;
        update(new Date());
    };
    return (
        <Box sx={sx.root}>
            <Typography
                sx={sx.header}
                gutterBottom
                color="textPrimary"
                variant="h6"
            >
                {schema.label}
            </Typography>
            <Grid sx={sx.grid} container spacing={2}>
                {items.map((row, index) => {
                    const { type, label, field, xs, sm, md, lg, xl } = row;
                    if (row.type === "hidden") return null;
                    return (
                        <Grid
                            key={index}
                            item
                            xs={xs || 12}
                            sm={sm}
                            md={md}
                            lg={lg}
                            xl={xl}
                        >
                            <Card key={index}>
                                <CardContent>
                                    {type === "number" && (
                                        <TextField
                                            fullWidth
                                            variant="standard"
                                            label={label}
                                            type="number"
                                            error={error[field] !== undefined}
                                            sx={sx.textField}
                                            value={data[field]}
                                            onChange={handleChange(field)}
                                        />
                                    )}

                                    {type === "string" && (
                                        <TextField
                                            fullWidth
                                            variant="standard"
                                            label={label}
                                            error={error[field] !== undefined}
                                            sx={sx.textField}
                                            value={data[field]}
                                            onChange={handleChange(field)}
                                        />
                                    )}

                                    {type === "date" && (
                                        <LocalizationProvider
                                            dateAdapter={AdapterMoment}
                                        >
                                            <DatePicker
                                                label={label}
                                                error={
                                                    error[field] !== undefined
                                                }
                                                value={data[field]}
                                                onChange={(newValue) => {
                                                    handleChangeDateTime(field)(
                                                        newValue.format(
                                                            "YYYY-MM-DD"
                                                        )
                                                    );
                                                }}
                                                renderInput={(params) => (
                                                    <TextField
                                                        variant="standard"
                                                        {...params}
                                                    />
                                                )}
                                            />
                                        </LocalizationProvider>
                                    )}

                                    {type === "datetime" && (
                                        <LocalizationProvider
                                            dateAdapter={AdapterMoment}
                                        >
                                            <DateTimePicker
                                                label={label}
                                                error={
                                                    error[field] !== undefined
                                                }
                                                value={data[field]}
                                                onChange={(newValue) => {
                                                    handleChangeDateTime(field)(
                                                        newValue.toISOString()
                                                    );
                                                }}
                                                renderInput={(props) => (
                                                    <TextField
                                                        variant="standard"
                                                        {...props}
                                                    />
                                                )}
                                            />
                                        </LocalizationProvider>
                                    )}

                                    {type === "text" && (
                                        <TextField
                                            fullWidth
                                            multiline
                                            variant="standard"
                                            label={label}
                                            error={error[field] !== undefined}
                                            sx={sx.textField}
                                            value={data[field]}
                                            onChange={handleChange(field)}
                                        />
                                    )}

                                    {type === "boolean" && (
                                        <FormControlLabel
                                            control={
                                                <Checkbox
                                                    error={
                                                        error[field] !==
                                                        undefined
                                                    }
                                                    checked={data[field]}
                                                    onChange={handleChangeCheck(
                                                        field
                                                    )}
                                                    color="primary"
                                                    inputProps={{
                                                        "aria-label":
                                                            "primary checkbox",
                                                    }}
                                                />
                                            }
                                            label={label}
                                        />
                                    )}
                                </CardContent>
                            </Card>
                        </Grid>
                    );
                })}
            </Grid>
        </Box>
    );
};

export default EntityEditGroup;
