const storage = window.localStorage;

const Data = {
    apiURL: "http://143.198.230.78:8070",

    pacientes: [],
    hc: [],
    hcRelated: [],
    relatedDraw: 0,

    save: () => {
        try {
            const json = JSON.stringify({
                token: Data.token,
            });

            if (storage !== [][[]]) storage.setItem("clinical-history", json);
        } catch (error) {
            console.log(error);
        }
    },
    load: () => {
        try {
            let json = "";
            if (storage !== [][[]]) json = storage.getItem("clinical-history");

            const data = JSON.parse(json);
            if (data == null) return;
            Data.token = data.token;
        } catch (error) {
            console.log(error);
        }
    },
};

window.Data = Data;

export default Data;
