import Fetch from "./Fetch.js";

const HCAPI = {
    get: (data, callback = () => {}, error = () => {}) => {
        Fetch.get(
            "/api/historiaclinica/",
            data,
            (res) => {
                callback(res);
            },
            error
        );
    },
    filterList: (dni, page = 1, callback = () => {}, error = () => {}) => {
        Fetch.get(
            "/api/historiaclinicafilterlist/?paciente=" + dni + "&page=" + page,
            (res) => {
                callback(res);
            },
            error
        );
    },
    post: (data, callback = () => {}, error = () => {}) => {
        Fetch.post(
            "/api/historiaclinica/",
            data,
            (res) => {
                callback(res);
            },
            error
        );
    },
    put: (id, data, callback = () => {}, error = () => {}) => {
        Fetch.put(
            "/api/historiaclinica/" + id + "/",
            data,
            (res) => {
                callback(res);
            },
            error
        );
    },
    delete: (id, callback = () => {}, error = () => {}) => {
        Fetch.delete(
            "/api/historiaclinica/" + id,
            (res) => {
                callback(res);
            },
            error
        );
    },
};

export default HCAPI;
