import React, { useState } from "react";

import {
    FormControl,
    Grid,
    InputLabel,
    Paper,
    NativeSelect,
} from "@mui/material";
import { Skeleton } from "@mui/lab";

const sx = {
    root: {
        display: "flex",
        alignItems: "center",
        marginBottom: 2,
        justifyContent: "space-around",
    },
    group: {
        padding: "0 15px",
        justifyContent: "center",
    },
    label: {
        marginTop: 1,
        marginBottom: 1,
    },
    formControl: {
        margin: 1,
        minWidth: 120,
        flex: 1,
        width: "100%",
    },
};

const DocumentSelector = ({
    clinicalHistoryList,
    selectedHC,
    onChangeHC,
    relatedDoc,
    selectedDoc,
    onChangeDoc,
    loadingHC,
    loadingDoc,
}) => {
    const [openHC, setOpenHC] = useState(false);
    const [openDoc, setOpenDoc] = useState(false);

    const handleOpenHC = () => {
        setOpenHC(true);
    };

    const handleCloseHC = () => {
        setOpenHC(false);
    };

    const handleOpenDoc = () => {
        setOpenDoc(true);
    };

    const handleCloseDoc = () => {
        setOpenDoc(false);
    };

    return (
        <Paper sx={sx.root}>
            <Grid container>
                <Grid
                    container
                    item
                    xs={12}
                    sm={12}
                    md
                    alignItems="center"
                    sx={sx.group}
                >
                    <Grid item xs sm md>
                        <InputLabel id="select-HC" sx={sx.label}>
                            Historia clnica:
                        </InputLabel>
                    </Grid>
                    <Grid item xs={12} sm={8}>
                        {loadingHC ? (
                            <Skeleton height={48} />
                        ) : (
                            <FormControl sx={sx.formControl}>
                                <NativeSelect
                                    native
                                    labelId="select-HC"
                                    open={openHC}
                                    onClose={handleCloseHC}
                                    onOpen={handleOpenHC}
                                    value={selectedHC}
                                    onChange={onChangeHC}
                                >
                                    <option aria-label="None" value="">
                                        Ninguno
                                    </option>
                                    {clinicalHistoryList.map((row) => (
                                        <option key={row.id} value={row.id}>
                                            {row.motivo}
                                        </option>
                                    ))}
                                </NativeSelect>
                            </FormControl>
                        )}
                    </Grid>
                </Grid>

                <Grid container item xs sm md alignItems="center" sx={sx.group}>
                    <Grid item xs sm md>
                        <InputLabel id="select-Docs" sx={sx.label}>
                            Documento:
                        </InputLabel>
                    </Grid>

                    <Grid item xs={12} sm={8}>
                        {loadingDoc ? (
                            <Skeleton height={48} />
                        ) : (
                            <FormControl sx={sx.formControl}>
                                <NativeSelect
                                    native
                                    labelId="select-Docs"
                                    id="demo-controlled-open-select"
                                    open={openDoc}
                                    onClose={handleCloseDoc}
                                    onOpen={handleOpenDoc}
                                    value={selectedDoc}
                                    onChange={onChangeDoc}
                                >
                                    <option aria-label="None" value="">
                                        Ninguno
                                    </option>
                                    {relatedDoc.map((row) => (
                                        <option key={row.id} value={row.id}>
                                            {row.label}
                                        </option>
                                    ))}
                                </NativeSelect>
                            </FormControl>
                        )}
                    </Grid>
                </Grid>
            </Grid>
        </Paper>
    );
};

export default DocumentSelector;
