import React, { useState } from "react";
import {
    Box,
    Card,
    MobileStepper,
    Button,
    Grid,
    Typography,
} from "@mui/material";

import KeyboardArrowLeft from "@mui/icons-material/KeyboardArrowLeft";
import KeyboardArrowRight from "@mui/icons-material/KeyboardArrowRight";

import EntityGroup from "./EntityGroup";
import EntityEditGroup from "./EntityEditGroup";
import Notifications from "./Notifications";

const sx = {
    root: {
        height: "100%",
        marginBottom: 2,
        display: "flex",
        flexDirection: "column",
    },
    container: {
        height: "100%",
        width: "100%",
        overflow: "auto",
        paddingTop: 2,
    },
    editBtnGroup: {
        padding: 1,
        display: "flex",
        justifyContent: "center",
    },
    btn: {
        margin: 1,
        paddingLeft: 2,
        paddingRight: 2,
    },
};

const EntityContent = ({ doc, edit, setEdit, activeStep, setActiveStep }) => {
    const [error, setError] = useState(false);

    let { schema, data, api } = edit ? edit : doc === null ? {} : doc;

    const handleNext = () => {
        setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const end = activeStep >= schema && schema.length;

    const handleAccept = () => {
        if (data.id === undefined) {
            api.post(
                data,
                (res) => {
                    Notifications.success("Documento creado");
                    handleClose();
                },
                (res, isForm) => {
                    if (isForm)
                        Notifications.error("Error. Verifique el formulario");
                    else Notifications.error("Error de conexin.");
                    setError(res);
                }
            );
        } else {
            api.put(
                data.id,
                data,
                (res) => {
                    Notifications.success("Documento actualizado");
                },
                (res, isForm) => {
                    if (isForm)
                        Notifications.error("Error. Verifique el formulario");
                    else Notifications.error("Error de conexin.");
                    setError(res);
                }
            );
        }
    };

    const handleClose = () => {
        setEdit(false);
        setError(false);
    };
    EntityContent.clearError = () => {
        setError(false);
    };

    return (
        <Card elevation={3} sx={sx.root}>
            <Grid sx={sx.container}>
                {!schema && (
                    <Box
                        height="100%"
                        width="100%"
                        display="flex"
                        justifyContent="center"
                        alignItems="center"
                    >
                        <Typography variant="h5" color="textSecondary">
                            Seleccione un documento
                        </Typography>
                    </Box>
                )}
                {schema && !edit && !end && (
                    <EntityGroup data={data} schema={schema[activeStep]} />
                )}
                {schema && edit && !end && (
                    <EntityEditGroup
                        data={data}
                        schema={schema[activeStep]}
                        error={error}
                    />
                )}
            </Grid>
            {schema && schema.length > 1 && (
                <MobileStepper
                    variant="dots"
                    steps={schema.length}
                    position="static"
                    activeStep={activeStep}
                    sx={sx.stepper}
                    nextButton={
                        <Button
                            size="small"
                            onClick={handleNext}
                            disabled={activeStep === schema.length - 1}
                        >
                            Siguiente
                            <KeyboardArrowRight />
                        </Button>
                    }
                    backButton={
                        <Button
                            size="small"
                            onClick={handleBack}
                            disabled={activeStep === 0}
                        >
                            <KeyboardArrowLeft />
                            Antrs
                        </Button>
                    }
                />
            )}
            {edit && (
                <Box sx={sx.editBtnGroup}>
                    <Button
                        size="small"
                        color="primary"
                        variant="contained"
                        sx={sx.btn}
                        onClick={handleAccept}
                    >
                        Aceptar
                    </Button>
                    <Button
                        size="small"
                        sx={sx.btn}
                        onClick={handleClose}
                    >
                        Cerrar documento
                    </Button>
                </Box>
            )}
        </Card>
    );
};

EntityContent.clearError = () => {};

export default EntityContent;
