import React, { useState } from "react";
import { Backdrop, Box, Button, Fade, Modal, TextField } from "@mui/material";
import { AuthAPI } from "../API";

const sx = {
    modal: {
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
    },
    paper: {
        backgroundColor: "#fff",
        boxShadow: 5,
        zIndex: 1000,
        padding: 3,
    },
    content: {
        marginTop: 2,
        minWidth: 400,
        width: "100%",
        display: "flex",
        justifyContent: "center",
    },
    textField: {
        marginTop: 2,
    },
    btn: {
        margin: 1,
    },
};

const RegisterModal = () => {
    const [open, setOpen] = useState(false);
    const [values, setValues] = useState({
        username: "",
        email: "",
        password1: "",
        password2: "",
    });

    const handleChange = (prop) => (event) => {
        setValues({ ...values, [prop]: event.target.value });
    };

    const handleClose = () => {
        setOpen(false);
    };
    RegisterModal.show = () => {
        setOpen(true);
        setValues({
            username: "",
            email: "",
            password1: "",
            password2: "",
        });
    };
    RegisterModal.hide = () => {
        setOpen(false);
    };

    const handleRegister = () => {
        AuthAPI.registration(values, (res) => {
            console.log(res);
        });
    };

    return (
        <Modal
            disablePortal
            disableEnforceFocus
            disableAutoFocus
            open={open}
            onClose={handleClose}
            sx={sx.modal}
            closeAfterTransition
            BackdropComponent={Backdrop}
            BackdropProps={{
                timeout: 500,
            }}
            aria-labelledby="register-modal-title"
        >
            <Fade in={open}>
                <Box sx={sx.paper}>
                    <h2 id="register-modal-title">Registrar</h2>

                    <TextField
                        fullWidth
                        variant="standard"
                        id="user"
                        label="Usuario"
                        sx={sx.textField}
                        value={values.username}
                        onChange={handleChange("username")}
                    />
                    <TextField
                        fullWidth
                        variant="standard"
                        id="user"
                        label="Email"
                        sx={sx.textField}
                        value={values.email}
                        onChange={handleChange("email")}
                    />
                    <TextField
                        fullWidth
                        variant="standard"
                        id="user"
                        label="Contrasea"
                        sx={sx.textField}
                        value={values.password1}
                        type="password"
                        onChange={handleChange("password1")}
                    />
                    <TextField
                        fullWidth
                        variant="standard"
                        id="user"
                        label="Verificar contrasea"
                        sx={sx.textField}
                        value={values.password2}
                        type="password"
                        onChange={handleChange("password2")}
                    />
                    <Box sx={sx.content}>
                        <Button
                            variant="contained"
                            color="primary"
                            sx={sx.btn}
                            onClick={() => {
                                handleRegister();
                            }}
                        >
                            Aceptar
                        </Button>
                        <Button
                            variant="contained"
                            sx={sx.btn}
                            onClick={handleClose}
                        >
                            Cancelar
                        </Button>
                    </Box>
                </Box>
            </Fade>
        </Modal>
    );
};

RegisterModal.show = () => {};
RegisterModal.hide = () => {};

export default RegisterModal;
