import React, { useState } from "react";
import {
    Button,
    Box,
    Card,
    Divider,
    FormControl,
    Grid,
    IconButton,
    Input,
    InputAdornment,
    InputLabel,
    TextField,
    Typography,
} from "@mui/material";

import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";

import { AuthAPI } from "../API";
import Notifications from "../Components/Notifications";

import "./Login.css";

const sx = {
    root: {
        display: "flex",
        flexWrap: "wrap",
    },
    margin: {
        margin: 1,
    },
    textField: {
        marginTop: 2,
    },
    btn: {
        margin: 1,
    },
    btnContainer: {
        marginTop: 2,
        display: "flex",
        justifyContent: "center",
    },
};

const Login = ({ onAuth }) => {
    const [visible, setVisible] = useState(false);
    const [showPassword, setShowPassword] = useState(false);
    const [forgot, setForgot] = useState(false);
    const [change, setChange] = useState(false);
    const [values, setValues] = useState({
        username: "",
        password: "",
        email: "",
        new_password1: "",
        new_password2: "",
        token: "",
    });

    const handleChange = (prop) => (event) => {
        setValues({ ...values, [prop]: event.target.value });
    };

    const handleClickShowPassword = () => {
        setShowPassword(!showPassword);
    };

    const handleMouseDownPassword = (event) => {
        event.preventDefault();
    };

    const reset = () => {
        setForgot(false);
        setChange(false);
        setShowPassword(false);
        setValues({
            username: "",
            password: "",
            email: "",
            new_password1: "",
            new_password2: "",
            token: "",
        });
    };

    const onRequestChange = () => {
        AuthAPI.passwordResetConfirm(
            {
                new_password1: values.new_password1,
                new_password2: values.new_password2,
                token: values.token,
            },
            () => {
                reset();
                Notifications.success("Contrasea actualizada");
            },
            () => {
                Notifications.error("Error al cambiar contrasea");
            }
        );
    };

    const onChange = () => {
        AuthAPI.passwordReset(
            {
                new_password1: values.new_password1,
                new_password2: values.new_password2,
                token: values.token,
            },
            () => {
                Notifications.info("Solicitud enviada");
            },
            () => {
                Notifications.error("Error al solicitar");
            }
        );
    };

    const onSubmit = () => {
        AuthAPI.login(
            {
                username: values.username,
                password: values.password,
            },
            (res) => {
                // Data.token = res.key;
                Notifications.success("Autenticado");
                onAuth(true);
                setVisible(false);
                reset();
            },
            () => {
                Notifications.error("Error de autenticacin");
            }
        );
    };

    Login.show = () => {
        setVisible(true);
    };
    Login.hide = () => {
        setVisible(false);
    };

    if (!visible) return null;
    return (
        <Grid
            container
            direction="row"
            justifyContent="center"
            alignItems="center"
            className="login-form-container"
            style={{ height: "100vh" }}
        >
            <Card elevation={3} style={{ padding: 20 }}>
                <Typography variant="h5" gutterBottom>
                    {forgot ? "Solicitar cambio de contrasea" : "Autenticarse"}
                </Typography>

                <Divider />
                {change ? (
                    <React.Fragment>
                        <TextField
                            fullWidth
                            label="Token"
                            variant="standard"
                            sx={sx.textField}
                            value={values.token}
                            onChange={handleChange("token")}
                        />
                        <FormControl
                            fullWidth
                            sx={sx.textField}
                            variant="standard"
                        >
                            <InputLabel htmlFor="password">
                                Contrasea
                            </InputLabel>
                            <Input
                                id="password"
                                type={showPassword ? "text" : "password"}
                                value={values.new_password1}
                                onChange={handleChange("new_password1")}
                                endAdornment={
                                    <InputAdornment position="end">
                                        <IconButton
                                            aria-label="toggle password visibility"
                                            onClick={handleClickShowPassword}
                                            onMouseDown={
                                                handleMouseDownPassword
                                            }
                                        >
                                            {showPassword ? (
                                                <Visibility />
                                            ) : (
                                                <VisibilityOff />
                                            )}
                                        </IconButton>
                                    </InputAdornment>
                                }
                            />
                        </FormControl>
                        <FormControl
                            fullWidth
                            sx={sx.textField}
                            variant="standard"
                        >
                            <InputLabel htmlFor="password">
                                Repetir contrasea
                            </InputLabel>
                            <Input
                                id="password"
                                type={showPassword ? "text" : "password"}
                                value={values.new_password2}
                                onChange={handleChange("new_password2")}
                                endAdornment={
                                    <InputAdornment position="end">
                                        <IconButton
                                            aria-label="toggle password visibility"
                                            onClick={handleClickShowPassword}
                                            onMouseDown={
                                                handleMouseDownPassword
                                            }
                                        >
                                            {showPassword ? (
                                                <Visibility />
                                            ) : (
                                                <VisibilityOff />
                                            )}
                                        </IconButton>
                                    </InputAdornment>
                                }
                            />
                        </FormControl>

                        <Box sx={sx.btnContainer}>
                            <Button
                                variant="contained"
                                color="primary"
                                sx={sx.btn}
                                onClick={onChange}
                            >
                                Cambiar
                            </Button>
                            <Button
                                color="primary"
                                sx={sx.btn}
                                onClick={() => setChange(false)}
                            >
                                Cancelar
                            </Button>
                        </Box>
                    </React.Fragment>
                ) : forgot ? (
                    <React.Fragment>
                        <TextField
                            fullWidth
                            label="Email"
                            variant="standard"
                            sx={sx.textField}
                            value={values.email}
                            onChange={handleChange("email")}
                        />

                        <Box sx={sx.btnContainer}>
                            <Button
                                variant="contained"
                                color="primary"
                                sx={sx.btn}
                                onClick={onRequestChange}
                            >
                                Solicitar
                            </Button>
                            <Button
                                color="primary"
                                sx={sx.btn}
                                onClick={() => setForgot(false)}
                            >
                                Cancelar
                            </Button>
                        </Box>
                    </React.Fragment>
                ) : (
                    <React.Fragment>
                        <TextField
                            fullWidth
                            variant="standard"
                            label="Usuario"
                            sx={sx.textField}
                            value={values.username}
                            onChange={handleChange("username")}
                        />
                        <FormControl
                            fullWidth
                            sx={sx.textField}
                            variant="standard"
                        >
                            <InputLabel htmlFor="password">
                                Contrasea
                            </InputLabel>
                            <Input
                                id="password"
                                type={showPassword ? "text" : "password"}
                                value={values.password}
                                onChange={handleChange("password")}
                                endAdornment={
                                    <InputAdornment position="end">
                                        <IconButton
                                            aria-label="toggle password visibility"
                                            onClick={handleClickShowPassword}
                                            onMouseDown={
                                                handleMouseDownPassword
                                            }
                                        >
                                            {showPassword ? (
                                                <Visibility />
                                            ) : (
                                                <VisibilityOff />
                                            )}
                                        </IconButton>
                                    </InputAdornment>
                                }
                            />
                        </FormControl>

                        <Box sx={sx.btnContainer}>
                            <Button
                                variant="contained"
                                color="primary"
                                sx={sx.btn}
                                onClick={onSubmit}
                            >
                                Acceder
                            </Button>
                            <Button
                                color="primary"
                                sx={sx.btn}
                                onClick={Login.hide}
                            >
                                Cancelar
                            </Button>
                        </Box>
                    </React.Fragment>
                )}
                {!change &&
                    (forgot ? (
                        <Button
                            color="primary"
                            style={{ marginTop: "10px" }}
                            onClick={() => setChange(true)}
                        >
                            Cambiar contrasea
                        </Button>
                    ) : (
                        <Button
                            color="primary"
                            style={{ marginTop: "10px" }}
                            onClick={() => setForgot(true)}
                        >
                            Olvid la contrasea
                        </Button>
                    ))}
            </Card>
        </Grid>
    );
};

Login.show = () => {};
Login.hide = () => {};

export default Login;
