import Data from "../globals/Data.js";
import Fetch from "./Fetch.js";

const UserAPI = {
    login: (data, callback = () => {}, error = () => {}) => {
        Fetch.post(
            "/rest-auth/login/",
            data,
            (res) => {
                const { username, password } = data;
                Data.token = btoa(`${username}:${password}`);
                Data.save();
                callback(res);
            },
            error
        );
    },
    logout: (callback = () => {}, error = () => {}) => {
        Fetch.postVanilla(
            "/rest-auth/logout/",
            {},
            (res) => {
                Data.token = undefined;
                Data.save();
                callback(res);
            },
            error
        );
    },
    user: (callback = () => {}, error = () => {}) => {
        Fetch.get(
            "/rest-auth/user/",
            (res) => {
                callback(res);
            },
            error
        );
    },
    patch: (data, callback = () => {}, error = () => {}) => {
        Fetch.patch(
            "/rest-auth/user/",
            data,
            (res) => {
                callback(res);
            },
            error
        );
    },
    registration: (data, callback = () => {}, error = () => {}) => {
        Fetch.post(
            "/rest-auth/registration/",
            data,
            (res) => {
                callback(res);
            },
            error
        );
    },
    verifyEmail: (data, callback = () => {}, error = () => {}) => {
        Fetch.post(
            "/rest-auth/registration/verify-email/",
            data,
            (res) => {
                callback(res);
            },
            error
        );
    },
    passwordChange: (data, callback = () => {}, error = () => {}) => {
        Fetch.post(
            "/rest-auth/password/change/",
            data,
            (res) => {
                callback(res);
            },
            error
        );
    },
    passwordReset: (data, callback = () => {}, error = () => {}) => {
        Fetch.post(
            "/rest-auth/password/reset/",
            data,
            (res) => {
                callback(res);
            },
            error
        );
    },
    passwordResetConfirm: (data, callback = () => {}, error = () => {}) => {
        Fetch.post(
            "/rest-auth/password/reset/confirm/",
            data,
            (res) => {
                callback(res);
            },
            error
        );
    },
    // list: (callback = () => {}, error = () => {}) => {
    //     Fetch.get(
    //         Data.baseUrl + "/api/user/list",
    //         (res) => {
    //             Data.users = res;
    //             callback(res);
    //         },
    //         error,
    //     );
    // },
    // post: (data, callback = () => {}, error = () => {}) => {
    //     Fetch.post(
    //         Data.baseUrl + "/api/user",
    //         data,
    //         (res) => {
    //             Data.users.push(res);
    //             callback(res);
    //         },
    //         error,
    //     );
    // },
};

export default UserAPI;
