import Fetch from "./Fetch.js";

const antecedentesAPI = {
    filterList: (idClinicalHistory, page = 1, callback = () => {}, error = () => {}) => {
        Fetch.get(
            "/api/antecedentesfilterlist/?historia=" + idClinicalHistory + "&page=" + page,
            (res) => {
                callback(res);
            },
            error
        );
    },
    post: (data, callback = () => {}, error = () => {}) => {
        Fetch.post(
            "/api/antecedentes/",
            data,
            (res) => {
                callback(res);
            },
            error
        );
    },
    put: (id, data, callback = () => {}, error = () => {}) => {
        Fetch.put(
            "/api/antecedentes/" + id + "/",
            data,
            (res) => {
                callback(res);
            },
            error
        );
    },
    delete: (id, callback = () => {}, error = () => {}) => {
        Fetch.delete(
            "/api/antecedentes/" + id,
            (res) => {
                callback(res);
            },
            error
        );
    },
};

export default antecedentesAPI;
