import Fetch from "./Fetch.js";

const pacienteAPI = {
    get: (data, callback = () => {}, error = () => {}) => {
        Fetch.get(
            "/api/paciente/",
            data,
            (res) => {
                callback(res);
            },
            error
        );
    },
    filterList: (search, page = 1, callback = () => {}, error = () => {}) => {
        Fetch.get(
            "/api/pacientefilterlist/?dni=" + search + "&page=" + page,
            (res) => {
                callback(res);
            },
            error
        );
    },
    post: (data, callback = () => {}, error = () => {}) => {
        Fetch.post(
            "/api/paciente/",
            data,
            (res) => {
                callback(res);
            },
            error
        );
    },
    put: (id, data, callback = () => {}, error = () => {}) => {
        Fetch.put(
            "/api/paciente/" + id + "/",
            data,
            (res) => {
                callback(res);
            },
            error
        );
    },
    delete: (id, callback = () => {}, error = () => {}) => {
        Fetch.delete(
            "/api/paciente/" + id,
            (res) => {
                callback(res);
            },
            error
        );
    },
};

export default pacienteAPI;
