import Data from "../globals/Data";

const Fetch = {
    get: (url, callback = () => {}, error = () => {}) => {
        const headers = {
            Accept: "application/json",
        };
        if (Data.token) {
            headers["Authorization"] = "Basic " + Data.token;
        }
        fetch(Data.apiURL + url, {
            method: "get",
            headers: headers,
            credentials: "same-origin",
            compress: true,
        })
            .then((res) => {
                return res.json();
            })
            .then((res) => {
                callback(res);
            })
            .catch((err) => {
                error(err);
            });
    },

    post: (url, data, callback = () => {}, error = () => {}) => {
        data = JSON.stringify(data);
        const headers = {
            Accept: "application/json",
            "Content-Type": "application/json",
            "Content-Length": data.length,
        };
        if (Data.token) {
            headers["Authorization"] = "Basic " + Data.token;
        }
        fetch(Data.apiURL + url, {
            method: "post",
            headers: headers,
            credentials: "same-origin",
            compress: true,
            body: data,
        })
            .then(async (res) => {
                if (res.status >= 200 && res.status < 300)
                    return callback(await res.json());
                return error(await res.json(), true);
            })
            .catch((err) => {
                error(err, false);
            });
    },

    patch: (url, data, callback = () => {}, error = () => {}) => {
        data = JSON.stringify(data);
        const headers = {
            Accept: "application/json",
            "Content-Type": "application/json",
            "Content-Length": data.length,
        };
        if (Data.token) {
            headers["Authorization"] = "Basic " + Data.token;
        }
        fetch(Data.apiURL + url, {
            method: "patch",
            headers: headers,
            credentials: "same-origin",
            compress: true,
            body: data,
        })
            .then(async (res) => {
                if (res.status >= 200 && res.status < 300)
                    return callback(await res.json());
                return error(await res.json(), true);
            })
            .catch((err) => {
                error(err, false);
            });
    },

    postVanilla: (url, data, callback = () => {}, error = () => {}) => {
        data = JSON.stringify(data);
        const headers = {
            Accept: "application/json",
            "Content-Type": "application/json",
            "Content-Length": data.length,
        };
        if (Data.token) {
            headers["Authorization"] = "Basic " + Data.token;
        }
        fetch(Data.apiURL + url, {
            method: "post",
            headers: headers,
            credentials: "same-origin",
            compress: true,
            body: data,
        })
            .then(async (res) => {
                if (res.status >= 200 && res.status < 300)
                    return callback(await res.json());
                return error(await res.json(), true);
            })
            .catch((err) => {
                error(err, false);
            });
    },

    put: (url, data, callback = () => {}, error = () => {}) => {
        data = JSON.stringify(data);
        const headers = {
            Accept: "application/json",
            "Content-Type": "application/json",
            "Content-Length": data.length,
        };
        if (Data.token) {
            headers["Authorization"] = "Basic " + Data.token;
        }
        fetch(Data.apiURL + url, {
            method: "put",
            headers: headers,
            credentials: "same-origin",
            compress: true,
            body: data,
        })
            .then(async (res) => {
                if (res.status === 200) return callback(await res.json());
                return error(await res.json());
            })
            .catch((err) => {
                console.log(err);
                error(err);
            });
    },

    delete: (url, callback = () => {}, error = () => {}) => {
        const data = JSON.stringify({ sid: Data.sid });
        const headers = {
            Accept: "application/json",
            "Content-Type": "application/json",
            "Content-Length": data.length,
        };
        if (Data.token) {
            headers["Authorization"] = "Basic " + Data.token;
        }
        fetch(Data.apiURL + url, {
            method: "delete",
            headers: headers,
            credentials: "same-origin",
            compress: true,
            body: data,
        })
            .then((res) => {
                callback(res);
            })
            .catch((err) => {
                error(err);
            });
    },
};

export default Fetch;
