import React, { useState } from "react";
import {
    AppBar,
    Button,
    Container,
    IconButton,
    Toolbar,
    Typography,
} from "@mui/material/";

import "./Main.css";
import ClinicalHistoryAPI from "../API/ClinicalHistoryAPI";
import SearchBox from "../Components/SearchBox";
import DocumentSelector from "../Components/DocumentSelector";
import EntityContent from "../Components/EntityContent";
import Menu from '@mui/icons-material/Menu';

import Entity from "../Entity";
import Login from "./Login";
import SideMenu from "../Components/SideMenu";
import Data from "../globals/Data";

const sx = {
    root: {
        display: "flex",
        flexDirection: "column",
        height: "100vh",
    },
    menuButton: {
        marginLeft: 2,
    },
    title: {
        flexGrow: 1,
    },
};

let relatedDoc = [];
let document = null;

const Main = ({ auth }) => {
    const update = useState(null)[1];

    const [patient, setPatient] = useState(null);
    const [clinicalHistoryList, setClinicalHistoryList] = useState([]);
    const [selectedDoc, setSelectedDoc] = useState("");
    const [selectedHC, setSelectedHC] = useState("");
    const [edit, setEdit] = useState(false);
    const [loadingDoc, setloadingDoc] = useState(false);
    const [loadingHC, setLoadingHC] = useState(false);
    const [activeStep, setActiveStep] = useState(0);

    const onPatientChange = (event, value) => {
        setPatient(value);
        if (value !== null) {
            setLoadingHC(true);
            ClinicalHistoryAPI.filterList(value.id, undefined, (res) => {
                setLoadingHC(false);
                setClinicalHistoryList(res.results);
            });
        } else {
            setClinicalHistoryList([]);
        }
    };

    const handleClear = () => {
        relatedDoc = [];
        document = null;
        setClinicalHistoryList([]);
        setSelectedDoc("");
        setSelectedHC("");
        setPatient(null);
    };

    const handleChangeHC = (event) => {
        const { value } = event.target;
        setSelectedDoc("");
        setSelectedHC(value);
        setloadingDoc(true);
        relatedDoc = [];
        const draw = Data.relatedDraw + 1;
        Data.relatedDraw++;
        if (value !== null && value !== "") {
            Entity.forEach((entity) => {
                if (draw < Data.relatedDraw) return;
                entity.api.filterList(value, undefined, (res) => {
                    setloadingDoc(false);
                    if (draw < Data.relatedDraw) return;
                    if (res.count === 0) return;
                    res.results.forEach((element) => {
                        relatedDoc.push({
                            id: relatedDoc.length,
                            ...entity,
                            data: element,
                        });
                    });
                    update(new Date());
                });
            });
        } else {
            setloadingDoc(false);
            document = null;
        }
    };

    const handleChangeDoc = (event) => {
        const { value } = event.target;
        setActiveStep(0);
        setSelectedDoc(value);
        setEdit(false);
        document = value === "" ? null : relatedDoc[value];
    };

    Main.edit = (data) => {
        handleClear();
        setEdit(data);
    };

    return (
        <Container fixed sx={sx.root}>
            <AppBar position="static">
                <Toolbar>
                    <Typography variant="h6" sx={sx.title}>
                        Historias Clnicas
                    </Typography>
                    {!auth && (
                        <Button color="inherit" onClick={Login.show}>
                            Acceder
                        </Button>
                    )}
                    {auth && (
                        <IconButton
                            edge="end"
                            sx={sx.menuButton}
                            color="inherit"
                            aria-label="menu"
                            onClick={SideMenu.show}
                        >
                            <Menu />
                        </IconButton>
                    )}
                </Toolbar>
            </AppBar>

            {!edit && (
                <SearchBox
                    patient={patient}
                    onPatientChange={onPatientChange}
                    onClear={handleClear}
                />
            )}

            {!edit && (
                <DocumentSelector
                    clinicalHistoryList={clinicalHistoryList}
                    selectedHC={selectedHC}
                    onChangeHC={handleChangeHC}
                    relatedDoc={relatedDoc}
                    selectedDoc={selectedDoc}
                    onChangeDoc={handleChangeDoc}
                    loadingHC={loadingHC}
                    loadingDoc={loadingDoc}
                />
            )}

            <EntityContent
                doc={document}
                edit={edit}
                setEdit={setEdit}
                activeStep={activeStep}
                setActiveStep={setActiveStep}
            />
        </Container>
    );
};

Main.edit = () => {};

export default Main;
