import React, { useState } from "react";
import { Backdrop, Box, Button, Fade, Modal, TextField } from "@mui/material";
import { Skeleton } from "@mui/lab";

import { AuthAPI } from "../API";
import Notifications from "./Notifications";

const sx = {
    modal: {
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
    },
    paper: {
        backgroundColor: "#fff",
        boxShadow: 5,
        padding: 3,
        zIndex: 1000,
    },
    content: {
        marginTop: 2,
        minWidth: 400,
        width: "100%",
        display: "flex",
        justifyContent: "center",
    },
    textField: {
        marginTop: 2,
    },
    btn: {
        margin: 1,
    },
};

const DataModal = () => {
    const [open, setOpen] = useState(false);
    const [values, setValues] = useState({
        username: "",
        first_name: "",
        last_name: "",
    });
    const [loading, setLoading] = useState(true);

    const handleChange = (prop) => (event) => {
        setValues({ ...values, [prop]: event.target.value });
    };

    const handleClose = () => {
        setOpen(false);
    };
    DataModal.show = () => {
        setLoading(true);
        setOpen(true);
        setValues({
            username: "",
            first_name: "",
            last_name: "",
        });
        AuthAPI.user((user) => {
            setValues({ ...user, email: undefined });
            setLoading(false);
        });
    };
    DataModal.hide = () => {
        setOpen(false);
    };

    const handlePatch = () => {
        AuthAPI.patch(
            values,
            (res) => {
                Notifications.success("Datos actualizados");
                console.log(res);
            },
            (res) => {
                Notifications.error("Error al actualizar");
            }
        );
    };

    return (
        <Modal
            disablePortal
            disableEnforceFocus
            disableAutoFocus
            open={open}
            onClose={handleClose}
            sx={sx.modal}
            closeAfterTransition
            BackdropComponent={Backdrop}
            BackdropProps={{
                timeout: 500,
            }}
            aria-labelledby="register-modal-title"
        >
            <Fade in={open}>
                <Box sx={sx.paper}>
                    <h2 id="register-modal-title">Cambio de datos</h2>

                    {loading ? (
                        <React.Fragment>
                            <Skeleton height={68} />
                            <Skeleton height={68} />
                            <Skeleton height={68} />
                        </React.Fragment>
                    ) : (
                        <React.Fragment>
                            <TextField
                                fullWidth
                                variant="standard"
                                label="Usuario"
                                sx={sx.textField}
                                value={values.username}
                                onChange={handleChange("username")}
                            />
                            <TextField
                                fullWidth
                                variant="standard"
                                label="Nombre"
                                sx={sx.textField}
                                value={values.first_name}
                                onChange={handleChange("first_name")}
                            />
                            <TextField
                                fullWidth
                                variant="standard"
                                label="Apellidos"
                                sx={sx.textField}
                                value={values.last_name}
                                onChange={handleChange("last_name")}
                            />
                        </React.Fragment>
                    )}
                    <Box sx={sx.content}>
                        <Button
                            variant="contained"
                            color="primary"
                            sx={sx.btn}
                            onClick={() => {
                                handlePatch();
                            }}
                        >
                            Aceptar
                        </Button>
                        <Button
                            variant="contained"
                            sx={sx.btn}
                            onClick={handleClose}
                        >
                            Cancelar
                        </Button>
                    </Box>
                </Box>
            </Fade>
        </Modal>
    );
};

DataModal.show = () => {};
DataModal.hide = () => {};

export default DataModal;
