import React, { useState } from "react";
import {
    Backdrop,
    Box,
    Button,
    Fade,
    Grid,
    IconButton,
    Modal,
    TextField,
} from "@mui/material";
import {
    DataGrid,
    GridToolbarColumnsButton,
    GridToolbarContainer,
    GridToolbarDensitySelector,
    GridToolbarExport,
} from "@mui/x-data-grid";

import Add from "@mui/icons-material/Add";
import Assignment from "@mui/icons-material/Assignment";
import Delete from "@mui/icons-material/Delete";
import Edit from "@mui/icons-material/Edit";

import Data from "../globals/Data";
import { pacienteAPI } from "../API";
import { LoadingOverlay, NoRowsOverlay } from "./CustomDataGrid";
import ConfirmModal from "./ConfirmModal";
import Notifications from "./Notifications";
import HCModal from "./HCModal";

const sx = {
    modal: {
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
    },
    paper: {
        backgroundColor: "#fff",
        boxShadow: 5,
        padding: 3,
        zIndex: 1000,
    },
    content: {
        height: 400,
        minWidth: 350,
        width: "80vw",
        overflow: "auto",
    },
    textField: {
        marginTop: 2,
    },
    btnBox: {
        marginTop: 2,
    },
    btn: {
        margin: "0 10px",
    },
};

const deleteRow = (params) => {
    ConfirmModal.show("Eliminar paciente", () => {
        const { id } = params;
        pacienteAPI.delete(id, () => {
            Data.pacientes = Data.pacientes.filter((e) => e.id !== id);
            PatientModal.update();
        });
    });
};

const columns = [
    { field: "id", headerName: "ID", width: 90 },
    {
        field: "dni",
        headerName: "CID",
        width: 110,
        editable: true,
    },
    {
        field: "nombre",
        headerName: "Nombre",
        width: 130,
    },
    {
        field: "primer_apellido",
        headerName: "1er Apellido",
        width: 150,
    },
    {
        field: "segundo_apellido",
        headerName: "2do Apellido",
        width: 150,
    },
    {
        field: "edad",
        headerName: "Edad",
        type: "number",
        width: 110,
    },
    {
        field: "email",
        headerName: "E-mail",
        width: 150,
    },
    {
        field: "direccion",
        headerName: "Direccin",
        width: 150,
    },
    {
        field: "operations",
        headerName: "Operaciones",
        description: "Botones de accin sobre el paciente.",
        sortable: false,
        filterable: false,
        width: 160,
        renderCell: (params) => (
            <Box>
                <IconButton
                    color="primary"
                    onClick={() => PatientModal.form(params)}
                >
                    <Edit />
                </IconButton>

                <IconButton
                    color="primary"
                    onClick={() => HCModal.show(params)}
                >
                    <Assignment />
                </IconButton>

                <IconButton color="secondary" onClick={() => deleteRow(params)}>
                    <Delete />
                </IconButton>
            </Box>
        ),
    },
];

function validateEmail(email) {
    const re =
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}

const PatientModal = () => {
    const update = useState(null)[1];
    const [open, setOpen] = useState(false);
    const [loading, setLoading] = useState(true);
    const [values, setValues] = useState(false);

    const handleClose = () => {
        setOpen(false);
    };
    PatientModal.update = () => update(new Date());
    PatientModal.show = () => {
        setLoading(true);
        pacienteAPI.get(
            (data) => {
                if (data.detail) {
                    console.log("ERROR");
                    console.log(data.detail);
                    return;
                }
                Data.pacientes = data;
                setLoading(false);
            },
            (err) => {
                console.log("ERROR");
                console.log(err);
            }
        );
        setOpen(true);
    };
    PatientModal.hide = () => {
        setOpen(false);
    };
    PatientModal.form = (params) => {
        if (params) {
            setValues(params.row);
        } else {
            setValues({
                id: "",
                dni: "",
                nombre: "",
                primer_apellido: "",
                segundo_apellido: "",
                edad: "",
                email: "",
                direccion: "",
            });
        }
    };

    return (
        <Modal
            disablePortal
            disableEnforceFocus
            disableAutoFocus
            aria-labelledby="transition-modal-title"
            sx={sx.modal}
            open={open}
            onClose={handleClose}
            closeAfterTransition
            BackdropComponent={Backdrop}
            BackdropProps={{
                timeout: 500,
            }}
        >
            <Fade in={open}>
                <Box sx={sx.paper}>
                    <h2 id="transition-modal-title">Paciente</h2>
                    <Box sx={sx.content}>
                        {values && (
                            <PatientForm
                                values={values}
                                setValues={setValues}
                            />
                        )}
                        <ValueGetterGrid loading={loading} />
                    </Box>
                </Box>
            </Fade>
        </Modal>
    );
};

PatientModal.update = () => {};
PatientModal.show = () => {};
PatientModal.hide = () => {};
PatientModal.form = () => {};

export default PatientModal;

const PatientForm = ({ values, setValues }) => {
    const {
        id,
        dni,
        nombre,
        primer_apellido,
        segundo_apellido,
        edad,
        email,
        direccion,
    } = values;
    const [emailError, setEmailError] = useState(false);

    const accept = () => {
        if (emailError) return Notifications.error("Correo incorrecto");
        const n = edad * 1;
        if (isNaN(n) || n < 0 || n > 150)
            return Notifications.error("Edad incorrecta");
        if (
            [
                dni.trim(),
                nombre.trim(),
                primer_apellido.trim(),
                segundo_apellido.trim(),
                edad.trim(),
                email.trim(),
                direccion.trim(),
            ].includes("")
        )
            return Notifications.error("No puede haber campos vacos");
        const fun =
            id === ""
                ? pacienteAPI.post
                : (data, cb, e) => pacienteAPI.put(id, data, cb, e);
        ConfirmModal.show("Guardar cambios", () =>
            fun(
                {
                    dni,
                    nombre,
                    primer_apellido,
                    segundo_apellido,
                    edad,
                    email,
                    direccion,
                },
                (res) => {
                    if (id === "") Data.pacientes = [...Data.pacientes, res];
                    else
                        Data.pacientes = Data.pacientes.map((e) =>
                            e.id === id ? res : e
                        );
                    setValues(false);
                    PatientModal.update();
                }
            )
        );
    };

    const handleChange = (prop) => (event) => {
        const { value } = event.target;
        if (prop === "email") {
            setEmailError(!validateEmail(value));
        } else if (prop === "edad") {
            const n = value * 1;
            if (isNaN(n) || n < 0 || n > 150) return;
        }
        setValues({ ...values, [prop]: value });
    };

    return (
        <Grid container className="hide-next">
            <Grid item xs={12}>
                <label>
                    {id === "" ? "Agregar paciente" : "Editar paciente"}
                </label>
            </Grid>
            <Grid container item xs={12} spacing={2}>
                <Grid item xs={12} sm={12} md={4}>
                    <TextField
                        fullWidth
                        variant="standard"
                        label="Nombre"
                        inputProps={{ maxLength: 200 }}
                        sx={sx.textField}
                        value={nombre}
                        onChange={handleChange("nombre")}
                    />
                </Grid>
                <Grid item xs={12} sm={6} md={4}>
                    <TextField
                        fullWidth
                        variant="standard"
                        label="Primer Apellido"
                        inputProps={{ maxLength: 200 }}
                        sx={sx.textField}
                        value={primer_apellido}
                        onChange={handleChange("primer_apellido")}
                    />
                </Grid>
                <Grid item xs={12} sm={6} md={4}>
                    <TextField
                        fullWidth
                        variant="standard"
                        label="Segundo Apellido"
                        inputProps={{ maxLength: 200 }}
                        sx={sx.textField}
                        value={segundo_apellido}
                        onChange={handleChange("segundo_apellido")}
                    />
                </Grid>
                <Grid item xs={12} sm={6} md={4}>
                    <TextField
                        fullWidth
                        variant="standard"
                        label="# Carn de identidad"
                        inputProps={{ maxLength: 200 }}
                        sx={sx.textField}
                        value={dni}
                        onChange={handleChange("dni")}
                    />
                </Grid>
                <Grid item xs={12} sm={6} md={1}>
                    <TextField
                        fullWidth
                        variant="standard"
                        label="Edad"
                        inputProps={{ maxLength: 3 }}
                        sx={sx.textField}
                        type={"number"}
                        value={edad}
                        onChange={handleChange("edad")}
                    />
                </Grid>
                <Grid item xs={12} md={7}>
                    <TextField
                        fullWidth
                        variant="standard"
                        label="Email"
                        inputProps={{ maxLength: 200 }}
                        sx={sx.textField}
                        error={emailError}
                        value={email}
                        onChange={handleChange("email")}
                    />
                </Grid>
                <Grid item xs={12} sm={12}>
                    <TextField
                        fullWidth
                        variant="standard"
                        multiline
                        rowsMax={5}
                        label="Direccin"
                        inputProps={{ maxLength: 200 }}
                        sx={sx.textField}
                        value={direccion}
                        onChange={handleChange("direccion")}
                    />
                </Grid>
            </Grid>
            <Grid
                container
                justify="center"
                spacing={2}
                item
                xs={12}
                sx={sx.btnBox}
            >
                <Button
                    variant="contained"
                    color="primary"
                    sx={sx.btn}
                    onClick={accept}
                >
                    Aceptar
                </Button>
                <Button
                    variant="contained"
                    sx={sx.btn}
                    onClick={() => setValues(false)}
                >
                    Cancelar
                </Button>
            </Grid>
        </Grid>
    );
};

function CustomToolbar() {
    return (
        <GridToolbarContainer>
            <Button
                variant="contained"
                color="primary"
                size="small"
                startIcon={<Add />}
                onClick={() => PatientModal.form()}
            >
                Agregar
            </Button>
            <GridToolbarColumnsButton />
            <GridToolbarDensitySelector />
            <GridToolbarExport />
        </GridToolbarContainer>
    );
}

function ValueGetterGrid({ loading }) {
    return (
        <React.Fragment>
            <Box style={{ height: 400, width: "100%" }}>
                <DataGrid
                    rows={Data.pacientes}
                    columns={columns}
                    loading={loading}
                    density="compact"
                    components={{
                        LoadingOverlay: LoadingOverlay,
                        NoRowsOverlay: NoRowsOverlay,
                        Toolbar: CustomToolbar,
                    }}
                />
            </Box>
        </React.Fragment>
    );
}
